import React from 'react';

import DivButtonTest from './DivButtonTest';
import TimeStamp from './TimeStamp';

import './Notebook.css';

const Notebook = () => {
    return(
        <div>
            <div className="NotebookTitle">
                Notebook
            </div>
            <hr/>
            
            <div className="NotebookEntry">
                <TimeStamp />
            </div>
            <hr />

            <div className="NotebookEntry">
                <DivButtonTest />
            </div>
            
        </div>
    )
}

export default Notebook;